import React from 'react'
import { Container } from "./SliderElements"
import { Carousel } from "react-bootstrap"
import slider1 from "../../img/banner1.jpeg"
import slider2 from "../../img/banner2.jpg"
import slider3 from "../../img/banner3.jpg"
import { GiClamp } from 'react-icons/gi'


export const Slider = () => {
    return (
        <div>
            <Carousel >
                <Carousel.Item interval={1000} style={{ maxWidth: "100vw", maxHeight: "100vh" }}>
                    <img
                        className="d-block w-100"
                        src={slider1}
                        alt="First slide"
                        style={{ maxWidth: "100vw", maxHeight: "100vh" }}
                    />
                    <Carousel.Caption>
                        {/* <h3 style={{ fontSize: "3rem", color: "#7AB531" }}>We are Creative in Codó</h3> */}
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={500} style={{ maxWidth: "100vw", maxHeight: "100vh" }}>
                    <img
                        className="d-block w-100"
                        src={slider2}
                        alt="Third slide"
                        style={{ maxWidth: "100vw", maxHeight: "100vh", position: "relative" }}
                    />
                    <Carousel.Caption>
                        {/* <h3 style={{ fontSize: "3rem", color: "#7AB531" }}>We are Creative in Codó</h3> */}
                        {/* <p style={{fontSize: "2rem"}}>Informações sobre o primeiro Slide.</p>  */}
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item style={{ maxWidth: "100vw", maxHeight: "100vh" }}>
                    <img
                        className="d-block w-100"
                        src={slider3}
                        alt="Third slide"
                        style={{ maxWidth: "100vw", maxHeight: "100vh" }}
                    />
                    <Carousel.Caption>
                        {/* <h3 style={{ fontSize:"clamp(1.5rem, 2.5vw, 3rem)", color: "#000" }}>We are Creative in Codó</h3> */}
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
    )
}
