import React, {useEffect, useState} from 'react'
import { Link } from "react-scroll"
import {
    NavMenu,
    NavWrapper,
    NavIconWrap,
    NavLogoWrap,
    IconMenuLateral,
    SideBar,
    IconCloseSideBar,
    NavSideBar,
    SideBarUl,
    SideBarLi,
    LinkSideBar,
    NavMenuDesktop,
    MenuUl,
    MenuLi,
    LinkMenu
} from "./NavElements.js"
import LogoIMG from "../../img/logo.png";

export const Nav = ({ isOpen, toggleCloseIcon, show }) => {

          // nav transparente quando no topo
          var show = null;

          useEffect(() => {
              const handleScroll = () => {
                  show = window.scrollY > 80
                  if (show) {
                      document.querySelector("#menu").style.background="#fff";
                      document.querySelector("#menu").style.boxShadow="3px 2px 10px #b4b4b4";
                  } else {
                      document.querySelector("#menu").style.background="transparent";
                      document.querySelector("#menu").style.boxShadow="none";
                      document.querySelector("#menu").style.color="#000";
                  }
              }
              document.addEventListener('scroll', handleScroll)
              return () => {
                  document.removeEventListener('scroll', handleScroll)
              }
          }, [])
          // nav transparente quando no topo

    return (
        <>
            <NavMenu id="menu" >
                <NavWrapper show={show}>
                    <NavIconWrap>
                        <IconMenuLateral onClick={toggleCloseIcon} />
                    </NavIconWrap>

                    <NavLogoWrap>
                        <Link to="/">
                            <img src={LogoIMG} alt="Logomarca Constru Service" />
                        </Link>

                    </NavLogoWrap>

                    <SideBar isOpen={isOpen}>
                        <IconCloseSideBar onClick={toggleCloseIcon} />

                        <NavSideBar>
                            <SideBarUl>
                                <SideBarLi>
                                    <LinkSideBar to="/" onClick={toggleCloseIcon}>Home</LinkSideBar>
                                </SideBarLi>

                                <SideBarLi>
                                    <LinkSideBar to="/" onClick={toggleCloseIcon}>Quem somos</LinkSideBar>
                                </SideBarLi>

                                <SideBarLi>
                                    <LinkSideBar to="/servicos" onClick={toggleCloseIcon}>Portfólio</LinkSideBar>
                                </SideBarLi>

                                <SideBarLi>
                                    <LinkSideBar to="/portfolio" onClick={toggleCloseIcon}>Orçamentos</LinkSideBar>
                                </SideBarLi>

                                <SideBarLi>
                                    <LinkSideBar to="/contato" onClick={toggleCloseIcon}>Contato</LinkSideBar>
                                </SideBarLi>
                            </SideBarUl>
                        </NavSideBar>
                    </SideBar>


                    <NavMenuDesktop id="menu">
                        <MenuUl>
                            <MenuLi>
                                <LinkMenu to="/" onClick={toggleCloseIcon}>Home</LinkMenu>
                            </MenuLi>

                            <MenuLi>
                                <LinkMenu to="/about" onClick={toggleCloseIcon}>Quem somos</LinkMenu>
                            </MenuLi>

                            <MenuLi>
                                <LinkMenu to="/servicos" onClick={toggleCloseIcon}>Portfólio</LinkMenu>
                            </MenuLi>

                            <MenuLi>
                                <LinkMenu to="/portfolio" onClick={toggleCloseIcon}>Orçamentos</LinkMenu>
                            </MenuLi>

                            <MenuLi>
                                <LinkMenu to="/contato" onClick={toggleCloseIcon}>Contato</LinkMenu>
                            </MenuLi>

                        </MenuUl>
                    </NavMenuDesktop>
                </NavWrapper>
            </NavMenu>
        </>
    )
}
