import React, { useState, useEffect } from "react"
import { ThemeProvider } from 'styled-components';
import { GlobalStyle } from "./GlobalStyle.js"
import { Nav } from "./Components/Nav/Index"
import { Slider } from "./Components/Slider/Index"
import { Service } from "./Components/Service/Index"
import { OurRates } from "./Components/OurRates/Index"
import { OurTeam } from './Components/OurTime/Index'
import { History } from "././Components/TimeLine/Index"
import { Price } from "././Components/Price/Index"
import { OwlSlider } from "./Components/SilderOwl/Index"
import { Contact } from "./Components/Contact/Index"
import { Footer } from './Components/Footer/Index'
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


function App() {


  const [isOpenAtendOnline, setisOpenAtendOnline] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const toggleCloseIcon = () => {
    setIsOpen(!isOpen)
  }

  const theme = {
    primary_color: "#fff",
    secundary_color: "#7AB531",
    ternary_color: "#F5C32E",
    font_color: "#333",
    font_padrao: "Roboto, sans-serif",
    font_secundaria: "Abel, sans-serif",

  };

  return (
    <>

      <Router>
        <ThemeProvider theme={theme}>
          <GlobalStyle />
          <Nav isOpen={isOpen} toggleCloseIcon={toggleCloseIcon} />
          <Switch>
            <Route path="/" exact render={() => (
              <>
                <Slider />
                <Service />
                <OurRates />
                <OurTeam />
                <History />
                <Price />
                <OwlSlider />
                <Contact />
                <Footer />
              </>
            )}
            />
          </Switch>
        </ThemeProvider>
      </Router>


    </>
  );
}

export default App;
